import "./App.css";
import { useState } from "react";
import Toolbar from "./Toolbar";
import MintOneWhitelist from "./MintOneWhitelist";
import MintTwoWhitelist from "./MintTwoWhitelist";

function App() {
  const [accounts, setAccounts] = useState([]);
  const [isConnected, setIsConnected] = useState(false);

  return (
    <>
      <Toolbar
        accounts={accounts}
        setAccounts={setAccounts}
        isConnected={isConnected}
      />
      <div className="mint-button-div">
        <MintOneWhitelist
          accounts={accounts}
          setAccounts={setAccounts}
          className="connect"
          setIsConnected={setIsConnected} // pass setIsConnected down to the MintOneWhitelist component
        />
        <MintTwoWhitelist
          accounts={accounts}
          setAccounts={setAccounts}
          className="connect"
          setIsConnected={setIsConnected}
        />
      </div>
    </>
  );
}

export default App;
