const lowerCasedWhitelist = whitelistedAddresses.map((item) =>
  item.toLowerCase()
);
console.log(lowerCasedWhitelist);
const leafNodes = lowerCasedWhitelist.map((addr) =>
  keccak256(addr).toString("hex")
);
const merkleTree = new MerkleTree(leafNodes, keccak256, { sortPairs: true });

console.log(leafNodes);
console.log(merkleTree);

const rootHash = merkleTree.getRoot();
console.log("Whitelist Merkle Tree\n", merkleTree.toString());
console.log("Root Hash: ", rootHash);

const rootHashHex = "0x" + rootHash.toString("hex");
console.log(rootHashHex);

const MintOneWhitelist = ({ accounts }) => {
  const isConnected = Boolean(accounts.length);
  console.log(isConnected);
  const whitelistMintOne = async () => {
    if (isConnected) {
      try {
        const nftContract = new ethers.Contract(
          contractAddress,
          contractAbi.abi,
          signer
        );

        console.log("Initialize payment");
        const quantity = 2;
        const price = ethers.utils.parseEther(`${0.015 * quantity}`);

        const claimingAddress = accounts[0].toLowerCase();
        console.log(claimingAddress);
        const claimingIndex = lowerCasedWhitelist.indexOf(`${claimingAddress}`);
        console.log(claimingIndex);
        const hexProof = merkleTree.getHexProof(leafNodes[claimingIndex]);

        let nftTxn = await nftContract.whitelistMint(quantity, hexProof, {
          value: price,
        });

        console.log("Mining... please wait");
        await nftTxn.wait();
        console.log(
          `Mined, see transaction: https://sepolia.etherscan.io/tx/${nftTxn.hash}`
        );
      } catch (err) {
        console.log(err);
      }
    }
  };

  return (
    <div className="mint-section">
      {!isConnected}
      {isConnected && (
        <div className="connect-div">
          <button
            className="inside-mint-button"
            onClick={() => whitelistMintOne()}
          >
            Mint 2
          </button>
        </div>
      )}
    </div>
  );
};

export default MintOneWhitelist;
